<template>
  <div id="home">
    <div class="vx-row">
      <!-- CARD 9: DISPATCHED ORDERS -->
      <div class="vx-col w-full">
        <vx-card title="Perguntas Respondidas">
          <div slot="no-body" class="mt-4">
            <vs-table :data="questionsSeries" class="table-dark-inverted">
              <template>
                <vs-tr  v-for="item in questionsSeries" :key="item">
                  <vs-td>
                    <h2>Pergunta: </h2><span>{{item.description}}</span><br>
                    <h2>Resposta: </h2><span>{{item.response}}</span>
                  </vs-td>
                </vs-tr>
              </template>
            </vs-table>
          </div>

    <!-- CARD 8: Activity Timeline -->
    <div class="vx-col w-full lg:w-1/3 mb-base">
      <vx-card title="Activity Timeline">
        <vx-timeline :data="timelineData" />
      </vx-card>
    </div>
        </vx-card>
  </div>

</div>
  </div>
</template>

<script>

import axios from "axios"

export default {
    data() {
        return {
          questionsSeries: ''
        }
    },
created(){
  this.request()
  },
  methods: {
    request(){
      axios.post('https://olist-interaction-manager-ci2nkxuhta-uc.a.run.app/api/v1/question/find-all')
    .then((response) => {
      console.log(response);
      this.questionsSeries = response.data
    }, (error) => {
      console.log(error);
    });
      }
}
}
</script>
